<template>
	<view>
		<u-swiper :list="list1" @change="change" :radius="0" @click="click" indicator></u-swiper>
		<u-grid :border="false" col="3" @click="click">
			<u-grid-item v-for="(listItem,listIndex) in list" :key="listIndex"
				v-if="listItem.platform.indexOf(platfrom) !== -1">
				<u-icon :customStyle="{paddingTop:20+'rpx'}" :name="listItem.name" :size="60" color="#2a439c"></u-icon>
				<text class="grid-text">{{listItem.title}}</text>
			</u-grid-item>
		</u-grid>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				platfrom: "",
				value: 0,
				list: [{
						name: 'search',
						title: '初检信息录入',
						platform: ["APP", "WEIXIN", "H5"],
					},
					{
						name: 'bell',
						title: '入库前质检',
						platform: ["APP", "WEIXIN", "H5"],
					},
					{
						name: 'bell-fill',
						title: '有损检验',
						platform: ["APP", "WEIXIN", "H5"],
					},
					{
						name: 'edit-pen',
						title: '回货暂收',
						platform: ["APP", "WEIXIN", "H5"],
					},
					{
						name: 'order',
						title: '创建送检单',
						platform: ["APP", "WEIXIN", "H5"],
					},
					{
						name: 'list-dot',
						title: '送检单明细',
						platform: ["APP", "WEIXIN", "H5"],
					},
					{
						name: 'list',
						title: '取检确认',
						platform: ["APP", "WEIXIN", "H5"],
					},
					{
						name: 'car',
						title: '逐条拣货配货',
						platform: ["APP", "WEIXIN", "H5"],
					},
					{
						name: 'car-fill',
						title: '整单配货',
						platform: ["APP", "WEIXIN", "H5"],
					},
					{
						name: 'grid',
						title: '交接',
						platform: ["APP", "WEIXIN", "H5"],
					},
					{
						name: 'coupon',
						title: '上架',
						platform: ["APP", "WEIXIN", "H5"],
					},
					{
						name: 'coupon-fill',
						title: '库存上架',
						platform: ["APP", "WEIXIN", "H5"],
					},
					{
						name: 'integral',
						title: '盘点',
						platform: ["APP", "WEIXIN", "H5"],
					},
					{
						name: 'star',
						title: 'RFID',
						platform: ["APP"],
					},
				],
				list1: [
					'/static/1.png',
					'/static/2.png',
					'/static/3.png',
				]
			}
		},
		onLoad() {

		},
		beforeCreate() {

		},
		mounted() {
			//页面加载完成获取枚举值并缓存在本地

			this.getAngecy(); //送检机构
			this.getInspectionCategory(); //送检单类型
			this.getReason(); //不合格原因
			this.getTestingCategory(); //质检类别

		},
		created() {
			//判断平台类型    设置模块是否显示
			let platform = undefined;
			// #ifdef APP-PLUS
			platform = "APP";
			// #endif
			// #ifdef APP-PLUS-NVUE
			platform = "APP";
			// #endif
			// #ifdef H5
			platform = "H5";
			// #endif
			// #ifdef MP-WEIXIN
			platform = "WEIXIN";
			// #endif
			this.platfrom = platform;
			console.log("========================= platform :", this.platfrom);
		},
		methods: {
			click(name) {
				switch (name) {
					case 0:
						uni.navigateTo({
							url: '/pages_home/qualityTesting'
						});
						break;
					case 1:
						uni.navigateTo({
							url: '/pages_home/frontTesting'
						});
						break;
					case 2:
						uni.navigateTo({
							url: '/pages_home/damageTesting'
						});
						break;

					case 3:
						uni.navigateTo({
							url: '/pages_home/provisionalReceipt'
						});
						break;
					case 4:
						uni.navigateTo({
							url: '/pages_home/createInspection'
						});
						break;
					case 5:
						uni.navigateTo({
							url: '/pages_home/inspectionDetail'
						});
						break;
					case 6:
						uni.navigateTo({
							url: '/pages_home/inspectionConfirm'
						});
						break;
					case 7:
						uni.navigateTo({
							url: '/pages_home/pickingManagement'
						});
						break;
					case 8:
						uni.navigateTo({
							url: '/pages_home/wholeOrderDistribution'
						});
						break;
					case 9:
						uni.navigateTo({
							url: '/pages_home/handover'
						})
						break;
					case 10:
						uni.navigateTo({
							url: '/pages_home/launch'
						});
						break;
					case 11:
						uni.navigateTo({
							url: '/pages_home/stockLaunch'
						});
						break;
					case 12:
						uni.navigateTo({
							url: '/pages_home/stockCheck'
						});
						break;
						case 13:
							uni.navigateTo({
								url: '/pages_home/RFIDDemo'
							});
							break;

					default:
						uni.navigateTo({
							url: '/pages_home/qualityTesting'
						});
				}
			},
			getAngecy() {
				//获取检测机构
				var opts = {
					url: ``,
					method: 'post'
				};
				var param = {
					"interface_num": "MOBSCMD0005",
					"serial_no": "123456789",
					"access_token": "abc",
					"bus_data": {
						"enumCode": "PO_CHECK_TYPE"
					},
				};
				this.$http.httpRequest(opts, param).then((res) => {
					if (res.data.code === "200") {
						var organList = [];
						if (res.data.data.PO_CHECK_TYPE.length != 0) {
							for (let i in res.data.data.PO_CHECK_TYPE) {
								organList.push({
									"text": res.data.data.PO_CHECK_TYPE[i].name,
									"value": res.data.data.PO_CHECK_TYPE[i].code,
								});
							}
						}
						uni.setStorage({
							key: 'organList',
							data: organList,
							success: function() {}
						});
					} else {
						this.$toast.showToast("获取检测机构失败，请刷新页面");

					}
				});
			},
			getInspectionCategory() {
				//获取送检单类别
				var opts = {
					url: ``,
					method: 'post'
				};
				var param = {
					"interface_num": "MOBSCMD0005",
					"serial_no": "123456789",
					"access_token": "abc",
					"bus_data": {
						"enumCode": "INSPECT_TYPE"
					},
				};
				this.$http.httpRequest(opts, param).then((res) => {
					if (res.data.code === "200") {
						var categoryList = [];
						if (res.data.data.INSPECT_TYPE.length != 0) {
							for (let i in res.data.data.INSPECT_TYPE) {
								categoryList.push({
									"text": res.data.data.INSPECT_TYPE[i].name,
									"value": res.data.data.INSPECT_TYPE[i].code,
								});
							}
						}
						uni.setStorage({
							key: 'categoryList',
							data: categoryList,
							success: function() {}
						});
					} else {
						this.$toast.showToast("获取送检单类别失败，请刷新页面");

					}
				});
			},
			getReason() {
				//获取不合格原因
				var opts = {
					url: ``,
					method: 'post'
				};
				var param = {
					"interface_num": "MOBSCMD0005",
					"serial_no": "123456789",
					"access_token": "abc",
					"bus_data": {
						"enumCode": "TEMPREC_UNQ_REASON"
					},
				};
				this.$http.httpRequest(opts, param).then((res) => {
					if (res.data.code === "200") {
						var reasonList = [];
						if (res.data.data.TEMPREC_UNQ_REASON.length != 0) {
							for (let i in res.data.data.TEMPREC_UNQ_REASON) {
								reasonList.push({
									"text": res.data.data.TEMPREC_UNQ_REASON[i].name,
									"value": res.data.data.TEMPREC_UNQ_REASON[i].code,
								});
							}
						}
						uni.setStorage({
							key: 'reasonList',
							data: reasonList,
							success: function() {}
						});
					} else {
						this.$toast.showToast("获取不合格原因失败，请刷新页面");

					}
				});
			},
			getTestingCategory() {
				//获取质检类别
				var opts = {
					url: ``,
					method: 'post'
				};
				var param = {
					"interface_num": "MOBSCMD0005",
					"serial_no": "123456789",
					"access_token": "abc",
					"bus_data": {
						"enumCode": "CHECK_CATEGORY"
					},
				};
				this.$http.httpRequest(opts, param).then((res) => {
					if (res.data.code === "200") {
						var typeList = [];
						if (res.data.data.CHECK_CATEGORY.length != 0) {
							for (let i in res.data.data.CHECK_CATEGORY) {
								typeList.push({
									"text": res.data.data.CHECK_CATEGORY[i].name,
									"value": res.data.data.CHECK_CATEGORY[i].code,
								});
							}
						}
						uni.setStorage({
							key: 'typeList',
							data: typeList,
							success: function() {}
						});
					} else {
						this.$toast.showToast("获取质检类别失败，请刷新页面");

					}
				});
			},
		}
	}
</script>

<style>

